/* Header component styling */
.image-container img{
   color:white;
   filter: brightness(0) invert(1);
   height:30px;
}
 .pencil{
    font-size: 1.2rem;
 }
 .heading{
    height:4rem;
    background-color: rgb(1,1,1);
    color: white;
    font-size: 1.5rem;
    font-family: "Lato", "Lucida Grande", "Lucida Sans Unicode", Tahoma, Sans-Serif;
    border-bottom: 1px solid rgb(47,47,47);
 }
 .head-title{
    background-color:black;
    color: white;
    outline: none;
    border: 0px;
    font-size: 1.3rem;
    font-family: Lato", "Lucida Grande", "Lucida Sans Unicode"," Tahoma, Sans-Serif;
 }
 .name-owner p{
   font-size: 0.875rem;
   color: rgb(155,157,173);
   font-family: Lato", "Lucida Grande", "Lucida Sans Unicode"," Tahoma, Sans-Serif;
}
 .input-area{
    height: 40vh;
    margin-left: 5%;
    background-color: rgb(29,30,34);
    color: white;
    width: 100%;
    font-family: SFMono-Regular, Consolas, "Liberation Mono", Menlo, monospace;
    font-size: 14px;
    font-weight:bold;
    border: none;
 }
 
 .input-area:focus {
    outline: none;  
}



/* editor component styling */
/* content area styling */
 .section{
    width: 32%;
 }
 .section-head{
    color:rgb(170, 174, 188);
    position: relative;
    top:5px;
    left:21px;
    padding: 1% 5% 1% 0%;
    font-size: 16.5px;
    background-color: rgb(29,30,34);
    font-family: Lato, "Lucida Grande", "Lucida Sans Unicode", Tahoma, sans-serif;
 }

 /* display section code  */
 .display{
    display: relative;
    width: 100%;
    height: 270px; 
    border: 5px solid rgb(29,30,34);
 }
.iframe {
    flex-grow: 1;
    border: none;
    width: 100%;  
   
    border: none;
}

/* footer component styling styling */
.footer{
    background-color: rgb(44,48,58);
}
.buttons{
    background-color: rgb(68,72,87);
    margin: 10px;
    padding: 5px;
    color: white;
}